import {
    DASHBOARD_COUNT
    
} from "../actions/types";

const initialState = {
    dashboard_count:[],

}


export default (state = initialState, action) => {
    
    const { type, payload } = action;
    switch (type) {
            case  DASHBOARD_COUNT:
            return {
                ...state,
                dashboard_count: payload.data
            };
           
           default:
            return state;
    }
}
